import React from 'react';
import View from '@vkontakte/vkui/dist/components/View/View';
//import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';
import {Epic, ModalRoot, FormLayout, Tabbar, TabbarItem, Panel, Button, Group} from '@vkontakte/vkui/dist';
import Icon28Newsfeed from '@vkontakte/icons/dist/28/newsfeed';
import Icon28More from '@vkontakte/icons/dist/28/more';
import Icon28UserCircleOutline from '@vkontakte/icons/dist/28/user';
import {ModalCard, Snackbar} from '@vkontakte/vkui/dist';
import {PanelHeaderBack, PanelHeader} from '@vkontakte/vkui/dist';

import Home from './panels/Home';
import Menu from './panels/Menu';
import News from './panels/News';
import PD from './panels/pd/PD';
import PSA from './panels/psa/PSA';
import Y from './panels/y/Y';
import PD197 from './panels/pd/PD-197';
import PD198 from './panels/pd/PD-198';
import PD199 from './panels/pd/PD-199';
import PD298 from './panels/pd/PD-298';
import PD298a from './panels/pd/PD-298a';
import PD299 from './panels/pd/PD-299';
import Raspisanie from './panels/raspisanie/raspisanie';

const MODAL_CARD_MONEY_SEND = 'money-send';

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeStory: 'Home',
      activePanel: "Menu",
      text: '',
      snackbar: null,
      activeModal: null,
      modalHistory: []
    }
    this.onStoryChange = this.onStoryChange.bind(this);
    this.openLongText = this.openLongText.bind(this);
    this.modalBack = () => {
      this.setActiveModal(this.state.modalHistory[this.state.modalHistory.length - 2]);
    };
  }

  onStoryChange (e) {
    this.setState({ activeStory: e.currentTarget.dataset.story })
  }

  go = e => {
	this.setState({ activePanel: e.currentTarget.dataset.to })
  }



  openLongText () {
    if (this.state.snackbar) return;
    this.setState({ snackbar:
      <Snackbar
        layout="vertical"
        onClose={() => this.setState({ snackbar: null })}
        action="Подробнее"
        onActionClick={() => this.setState({ text: 'Открыта подробная информация.' })}
      >
        Ссылка сохранена в закладки. Все отметки «Нравится» переместились в новости
        под вкладкой «Понравилось».
      </Snackbar>
    });
  }
  setActiveModal(activeModal) {
    activeModal = activeModal || null;
    let modalHistory = this.state.modalHistory ? [...this.state.modalHistory] : [];

    if (activeModal === null) {
      modalHistory = [];
    } else if (modalHistory.indexOf(activeModal) !== -1) {
      modalHistory = modalHistory.splice(0, modalHistory.indexOf(activeModal) + 1);
    } else {
      modalHistory.push(activeModal);
    }

    this.setState({
      activeModal,
      modalHistory
    });
  };


  render () {
  	const modal = (
      <ModalRoot
        activeModal={this.state.activeModal}
        onClose={this.modalBack}
      >
        <ModalCard
          id={MODAL_CARD_MONEY_SEND}
          onClose={() => this.setActiveModal(null)}
          header="Вот тут будет расписание"
          caption="И тут"
        >

        </ModalCard>
      </ModalRoot>
    );
    return (
      <Epic activeStory={this.state.activeStory} tabbar={
        <Tabbar>
          <TabbarItem
          	text="Новости"
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'News'}
            data-story="News"
          ><Icon28Newsfeed /></TabbarItem>
          <TabbarItem
          	text="Главная"
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'Home'}
            data-story="Home"
          ><Icon28UserCircleOutline /></TabbarItem>
          <TabbarItem
          	text="Меню"
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'Menu'}
            data-story="Menu"
          ><Icon28More /></TabbarItem>
        </Tabbar>
      }>
		<View id="News" activePanel="News">
 		 <News id="News" go={this.go} />
		</View>
		<View id="Home" activePanel="Home">
 		 <Home id="Home" go={this.go} />
		</View>
        <View id="Menu" activePanel={this.state.activePanel} modal={modal} className="background">
          <Menu id="Menu" go={this.go}/>
          <Raspisanie id="raspisanie" go={this.go}/>
          <PD id="pd" go={this.go}/>
          <Y id="y" go={this.go}/>
          <PSA id="psa" go={this.go}/>
          <PD197 id="pd197" go={this.go}/>
          <PD198 id="pd198" go={this.go}/>
          <PD199 id="pd199" go={this.go}/>
          <PD298 id="pd298" go={this.go}/>
          <PD298a id="pd298a" go={this.go}/>
          <PD299 id="pd299" go={this.go}/>
          <Panel id="rasp_pd197" go={this.go}>
          <PanelHeader left={<PanelHeaderBack onClick={this.go} data-to="pd197"/>}>
            Расписание
          </PanelHeader>
           <Group>
            <FormLayout>
             <Button size="xl" mode="tertiary" onClick={() => this.setActiveModal(MODAL_CARD_MONEY_SEND)}>
                    Вот тут будет расписание
             </Button>
            </FormLayout>
           </Group>
          </Panel>
          <Panel id="rasp_pd198" go={this.go}>
          <PanelHeader left={<PanelHeaderBack onClick={this.go} data-to="pd198"/>}>
            Расписание
          </PanelHeader>
          <Group>
            <FormLayout>
              <Button size="xl" mode="tertiary" onClick={() => this.setActiveModal(MODAL_CARD_MONEY_SEND)}>
                  Вот тут будет расписание
              </Button>
            </FormLayout>
          </Group>
          </Panel>
          <Panel id="rasp_pd199" go={this.go}>
          <PanelHeader left={<PanelHeaderBack onClick={this.go} data-to="pd199"/>}>
            Расписание
          </PanelHeader>
          <Group>
            <FormLayout>
              <Button size="xl" mode="tertiary" onClick={() => this.setActiveModal(MODAL_CARD_MONEY_SEND)}>
                  Вот тут будет расписание
              </Button>
            </FormLayout>
          </Group>
          </Panel>
          <Panel id="rasp_pd298" go={this.go}>
          <PanelHeader left={<PanelHeaderBack onClick={this.go} data-to="pd298"/>}>
            Расписание
          </PanelHeader>
          <Group>
            <FormLayout>
              <Button size="xl" mode="tertiary" onClick={() => this.setActiveModal(MODAL_CARD_MONEY_SEND)}>
                  Вот тут будет расписание
              </Button>
            </FormLayout>
          </Group>

          </Panel>
          <Panel id="rasp_pd298a" go={this.go}>
          <PanelHeader left={<PanelHeaderBack onClick={this.go} data-to="pd298a"/>}>
            Расписание
          </PanelHeader>
          <Group>
            <FormLayout>
              <Button size="xl" mode="tertiary" onClick={() => this.setActiveModal(MODAL_CARD_MONEY_SEND)}>
                  Вот тут будет расписание
              </Button>
            </FormLayout>
          </Group>
          
          </Panel>
          <Panel id="rasp_pd299" go={this.go}>
          <PanelHeader left={<PanelHeaderBack onClick={this.go} data-to="pd299"/>}>
            Расписание
          </PanelHeader>
          <Group>
            <FormLayout>
              <Button size="xl" mode="tertiary" onClick={() => this.setActiveModal(MODAL_CARD_MONEY_SEND)}>
                  Вот тут будет расписание
              </Button>
            </FormLayout>
          </Group>
          
          </Panel>
        </View>
      </Epic>

    )
  }
}

export default App;