import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import {Group, Div, Button, Text} from '@vkontakte/vkui/dist';

const Menu = props => (
	<Panel id={props.id}>
		<PanelHeader>Меню</PanelHeader>
		<Group title="Navigation Example">
			<Div>
				<Text className="Menutext1" weight="regular" style={{ marginBottom: 16 }}><h4>Выберите нужный пункт:</h4></Text>
				<Button size="xl" level="2" onClick={props.go} data-to="raspisanie">
					Выбрать свою группу
				</Button>
			</Div>
			<Div>
				<Button size="xl" level="2" onClick={props.go} data-to="y">
					Я здесь не обучаюсь, но хотел бы
				</Button>
			</Div>
		</Group>
	</Panel>
);

Menu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Menu;
